.login-page {
    .container {
        .wrapper {
            user-select: none;
            padding-top: 10rem;
            height: clamp(750px, 100vh, 100vh);
            .login-wrap {
                background-color: #fff;
                margin: 0 auto;
                animation: fadeUp 0.5s ease forwards;

                display: flex;
                flex-direction: column;
                width: clamp(200px, 100%, 500px);
                border: 1px solid rgb(218, 218, 218);
                padding: 2rem;
                box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                border-radius: 5px;
                h1 {
                    padding: 0rem 0 1rem 0;
                    text-align: center;
                    color: $text-primary;
                }
                label {
                    font-size: 13px;
                    color: $text-primary;
                }
                input {
                    border: 1px solid black;
                    border-radius: 5px;
                    height: 2rem;
                    color: $text-primary;
                }
                .reminder {
                    border-radius: 5px;
                    span {
                        color: white;
                        display: block;
                        text-align: center;
                        font-size: 13px;
                    }
                }
                .forgot-password {
                    font-size: 12px;
                    margin-top: 5px;
                    text-decoration: underline;
                    font-style: italic;
                    a {
                        color: $text-primary;
                    }
                }
                .join-whisper {
                    a {
                        color: $text-primary;
                    }
                }
                .submit-btn {
                    margin-top: 1rem;
                    background-color: #2250f4;
                    padding: 0.5rem 1rem 0.5rem 1rem;
                    border-radius: 5px;
                    border: none;
                    color: white;
                    font-weight: 600;
                    font-size: 0.8rem;
                    transition: 0.2s background-color ease;
                    &:hover {
                        background-color: #1b40c3;
                    }
                }
            }
        }
    }
}
