.nav-bar {
    top: 0;
    user-select: none;
    z-index: 999;
    position: fixed;
    width: 100%;
    padding: 0.6rem 0rem 0.6rem 0rem;
    background-color: white;
    box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
    animation: fadeDown 1s cubic-bezier(0, 0.69, 0.57, 1.01) forwards;
    .container {
        .wrapper {
            display: flex;
            justify-content: space-between;
            .nav-logo {
                img {
                    display: block;
                    max-width: 100%;
                    width: 250px;
                }
            }
            .nav-buttons {
                display: flex;
                justify-content: flex-end;
                .nav-button {
                    padding: 0 2rem 0 2rem;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    a {
                        text-decoration: none;
                        color: black;

                        display: flex;
                        flex-direction: column;
                        &:hover::after {
                            width: 100%;
                        }
                        &::after {
                            transition: 0.2s width ease;
                            content: "";
                            width: 0%;
                            height: 1px;
                            background-color: black;
                        }
                    }
                }
            }
            .profile {
                width: 50%;
                display: flex;
                justify-content: flex-end;
                align-items: center;

                ul {
                    list-style-type: none;
                    li {
                        color: $text-primary;
                        font-size: 14px;
                        position: relative;
                        i {
                            transition: 0.2s transform ease;
                            padding: 10px;
                        }
                        ul {
                            list-style-type: none;
                            position: absolute;
                            background-color: white;
                            width: 100%;
                            border-radius: 5px;
                            transition: 0.2s transform ease;
                            transform: scaleY(0%);
                            transform-origin: top;
                            box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                            overflow: hidden;
                            a {
                                text-decoration: none;
                            }
                            li {
                                cursor: pointer;
                                padding: 1rem;
                                transition: 0.3s transform ease;
                                i {
                                    color: black;
                                    padding: 0 0.5rem 0 0;
                                }
                            }
                            li:hover {
                                transform: translateX(10px);
                            }
                        }
                        &:hover .chev-down {
                            transform: rotate(180deg);
                        }
                        &:hover ul {
                            transform: scaleY(100%);
                        }
                    }
                }
            }
            .menu-btn {
                display: none;
            }
        }
    }
}

.mobile-nav-bar {
    transform: translateX(100%);
    transition: transform 0.4s ease;
    padding: 30px;
    position: fixed;
    height: 100%;
    width: 100%;
    color: white;
    z-index: 9999;
    background-color: $text-primary;
    .mobile-profile {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .close-mobile-nav {
            display: flex;
            justify-content: flex-end;
            padding-bottom: 1rem;
            i {
                color: red;
            }
        }
        ul {
            list-style-type: none;
            li {
                font-weight: bold;
                font-size: 12px;
                ul {
                    a {
                        text-decoration: none;
                        color: white;
                    }
                    li {
                        margin: 1rem 1rem 1rem 0;
                        padding: 1rem 1rem 1rem 0;
                        font-weight: lighter;
                        font-size: 20px;
                    }
                    i {
                        padding-right: 10px;
                    }
                    .logo-m_nav {
                        img {
                            max-width: 100%;
                            width: 100px;
                            display: block;
                        }
                    }
                }
            }
        }
        .mark {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            height: 100%;
            font-size: 10px;
            color: rgb(88, 88, 88);
            user-select: none;
        }
    }
}

@keyframes slideLeft {
    from {
        transform: translateX(10px);
        opacity: 0;
    }
    to {
        transform: translateX(0px);
        opacity: 1;
    }
}
