.add-friends {
    .container {
        .wrapper {
            color: $text-primary;
            display: flex;
            flex-direction: column;

            padding: 8rem 1rem 3rem 1rem;
            animation: fadeUp 0.5s ease;
            .body {
                .form {
                    form {
                        display: flex;
                        width: 100%;
                        input {
                            font-size: 30px;
                            width: 80%;
                            height: 40px;
                            border: 1px solid black;
                        }
                        button {
                            width: 20%;
                            height: 40px;
                            background-color: #3498db;
                            border: 1px solid black;
                            font-weight: bold;
                            color: white;

                            cursor: pointer;
                            transition: 0.2s background-color ease;
                            &:hover {
                                background-color: #2980b9;
                            }
                        }
                    }
                }
                .main-profile {
                    margin-top: 2rem;
                    padding: 1rem;
                    border-radius: 5px;
                    box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                    display: flex;
                    gap: 1rem;
                    animation: fadeUp 0.5s ease forwards;
                    border: 1px solid rgb(218, 218, 218);
                    .image {
                        flex-basis: 40%;
                        img {
                            display: block;
                            max-width: 100%;
                            object-fit: cover;
                            border-radius: 5px;
                            aspect-ratio: 1/1;
                        }
                        .no-image {
                            background-color: gray;
                            height: 100%;
                            width: 100%;
                            border-radius: 5px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            color: white;
                        }
                    }
                    .info {
                        flex-basis: 60%;
                        .info-main {
                            word-break: break-all;
                            .infox {
                                font-size: 35px;
                            }
                            button {
                                min-width: 200px;
                                padding: 0.5rem;
                                background-color: #3498db;
                                border: 1px solid black;
                                border-radius: 5px;
                                font-weight: bold;
                                color: white;

                                cursor: pointer;
                                transition: 0.2s background-color ease;
                                i {
                                    padding-right: 10px;
                                }
                                &:hover {
                                    background-color: #2980b9;
                                }
                            }
                            .yourself {
                                min-width: 200px;
                                border: 1px solid black;
                                border-radius: 5px;
                                background-color: #e74c3c;
                                padding: 0.5rem;
                                font-weight: bold;
                                color: white;
                            }
                        }
                    }
                }
                .user {
                    margin-top: 2rem;
                    padding: 1rem;
                    border-radius: 5px;
                    border: 1px solid rgb(218, 218, 218);
                    box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                    display: grid;
                    grid-template-columns: 1fr 2fr;
                    gap: 1rem;
                    animation: fadeUp 0.5s ease forwards;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: rgb(187, 187, 187);
                    user-select: none;
                }
            }
        }
    }
}
