.landing-page {
    .container {
        .wrapper {
            user-select: none;
            height: clamp(750px, 100vh, 100vh);
            display: flex;
            justify-content: center;
            align-items: center;
            .landing-text {
                display: flex;
                flex-direction: column;
                align-items: center;
                animation: fadeUp 0.5s ease forwards;

                text-align: center;
                color: $text-primary;
                h1 {
                    font-size: 55px;
                }
                span {
                    font-size: 20px;
                    max-width: 600px;
                    display: block;
                }
                .buttons {
                    margin-top: 1rem;
                    button {
                        background-color: #2250f4;

                        padding: 0.5rem 2rem 0.5rem 2rem;
                        border-radius: 5px;
                        border: none;
                        color: white;
                        font-weight: 600;
                        font-size: 1rem;
                        transition: 0.2s background-color ease, 0.3s transform ease;
                        &:hover {
                            background-color: #1b40c3;
                            transform: translateY(-5px);
                        }
                        a {
                            text-decoration: none;
                            color: white;
                        }
                    }
                }
                .markings {
                    margin-top: 1rem;
                    span {
                        user-select: none;
                        font-size: 12px;
                        color: rgb(175, 175, 175);
                    }
                }
            }
        }
    }
}
