.main-area {
    .container {
        .wrapper {
            animation: fadeUp 0.5s ease forwards;
            height: clamp(300px, 100vh, 800px);
            padding: 5rem 1rem 1rem 1rem;
            display: grid;
            grid-template-columns: 2fr 4fr;
            grid-template-areas: "side chat chat";
            .side-chat {
                user-select: none;
                margin: 10px;
                border-radius: 15px;
                grid-area: side;
                box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                border: 1px solid rgb(218, 218, 218);
                color: $text-primary;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                max-height: 700px;
                .title {
                    padding: 15px;
                    h1 {
                        text-align: center;
                    }
                }
                .chats {
                    height: 100%;
                    padding: 15px;
                    font-size: 14px;
                    text-align: center;
                    overflow-y: auto;
                    .chat-box {
                        cursor: pointer;
                        animation: fadeUp 0.5s ease forwards;
                        margin-top: 0.4rem;
                        padding: 0.3rem;
                        border-radius: 5px;
                        border: 1px solid rgb(218, 218, 218);
                        box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                        display: flex;
                        gap: 10px;
                        .image {
                            img {
                                max-width: 100%;
                                width: 45px;
                                object-fit: cover;
                                display: block;
                                border-radius: 50%;
                                aspect-ratio: 1/1;
                            }
                        }
                        .info {
                            .chat-username {
                                font-weight: bold;
                                text-align: left;
                                word-break: break-all;
                            }
                            .chat-latest-message {
                                text-align: left;
                                word-break: break-all;
                            }
                        }
                    }
                }
            }
            .main-chat {
                overflow: hidden;
                margin: 10px;
                border-radius: 15px;
                grid-area: chat;
                box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                border: 1px solid rgb(218, 218, 218);

                font-weight: light;
                max-height: 700px;
                .intro {
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: rgb(177, 177, 177);
                    user-select: none;
                }
                .fetching-messages {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 100%;
                }
                .chat-here {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    .chat-header {
                        border-bottom: 1px solid black;
                        padding: 0.5rem 1rem 0.5rem 1rem;
                        display: flex;
                        gap: 15px;
                        align-items: center;
                        justify-content: space-between;
                        user-select: none;
                        .chat-img {
                            img {
                                max-width: 100%;
                                display: block;
                                width: 40px;
                                border-radius: 50%;
                                aspect-ratio: 1/1;
                                object-fit: cover;
                            }
                        }
                        .chat-name {
                            span {
                                font-size: 18px;
                                word-break: break-all;
                            }
                        }
                        .close-button {
                            i {
                                color: red;
                                cursor: pointer;
                            }
                        }
                    }
                    .chat-body {
                        display: flex;
                        height: 100%;
                        width: 100%;
                        overflow-y: auto;
                        flex-direction: column;
                        .show-more {
                            display: flex;
                            justify-content: center;
                            padding: 1rem;
                            color: rgb(209, 209, 209);
                            text-decoration: underline;
                            cursor: pointer;
                        }
                        .message-left {
                            display: flex;
                            flex-direction: row;
                            align-items: center;
                            gap: 10px;
                            max-width: 90%;
                            margin: 0.2rem 0rem 0.2rem 1rem;
                            align-self: flex-start;
                            justify-content: flex-start;
                            width: fit-content;
                            word-break: break-all;
                            font-size: 14px;
                            color: white;
                            animation: fadeUp 0.2s ease forwards;
                            .image {
                                flex-shrink: 0;
                                height: 40px;
                                width: 40px;
                                img {
                                    object-fit: cover;
                                    max-width: 100%;
                                    border-radius: 50%;
                                    display: block;
                                }
                            }
                            .text {
                                padding: 1rem;
                                background-color: #27ae60;
                                border-radius: 5px;
                            }
                        }
                        .message-right {
                            align-self: flex-end;
                            padding: 1rem;
                            width: fit-content;
                            max-width: 90%;
                            word-break: break-all;
                            border-radius: 5px;
                            margin: 0.2rem 1rem 0.2rem 0;
                            font-size: 14px;
                            background-color: #2980b9;
                            color: white;
                            animation: fadeUp 0.2s ease forwards;
                        }
                    }
                    .loading-body {
                        height: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                    .chat-inputs {
                        border-top: 1px solid black;
                        form {
                            height: 100%;
                            input {
                                padding-left: 10px;
                                width: 90%;
                                height: 100%;

                                border: none;
                            }
                            button {
                                height: 100%;
                                width: 10%;
                                padding: 0.5rem;
                                background-color: #3498db;
                                border: none;
                                font-weight: bold;
                                color: white;

                                cursor: pointer;
                                transition: 0.2s background-color ease;
                                &:hover {
                                    background-color: #2980b9;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
