@media (max-width: 850px) {
    .profile {
        .container {
            .wrapper {
                .main-profile {
                    flex-direction: column;
                    .info {
                        .info-main {
                            .infox {
                                font-size: 25px;
                            }
                        }
                    }
                }
            }
        }
    }
    .friends {
        .container {
            .wrapper {
                h1 {
                    font-size: 30px;
                }
                .main-friends {
                    grid-template-columns: 1fr;
                }
            }
        }
    }

    .add-friends {
        .container {
            .wrapper {
                .body {
                    .form {
                        form {
                            input {
                                width: 70%;
                            }
                            button {
                                width: 30%;
                            }
                        }
                    }
                    .main-profile {
                        flex-direction: column;
                        .info {
                            .info-main {
                                .infox {
                                    font-size: 25px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 750px) {
    .main-area {
        .container {
            .wrapper {
                padding: 5rem 0.1rem 0.1rem 0.1rem;
                grid-template-columns: 1fr;
                .side-chat {
                    grid-area: unset;
                }
                .main-chat {
                    grid-area: unset;
                    .chat-here {
                        .chat-inputs {
                            form {
                                input {
                                    width: 70%;
                                }
                                button {
                                    width: 30%;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 600px) {
    .nav-bar {
        .container {
            .wrapper {
                justify-content: space-between;
                align-items: center;
                .nav-logo {
                    img {
                        width: 200px;
                    }
                }
                .nav-buttons {
                    display: none;
                }
                .profile {
                    display: none;
                }
                .menu-btn {
                    font-size: 25px;
                    display: block;
                }
            }
        }
    }

    .landing-page {
        .container {
            .wrapper {
                .landing-text {
                    h1 {
                        font-size: 45px;
                    }
                    span {
                        font-size: 15px;
                    }
                    .buttons {
                        button {
                            font-size: 0.8rem;
                        }
                    }
                    .markings {
                        span {
                            font-size: 10px;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 470px) {
    .friends {
        .container {
            .wrapper {
                .main-friends {
                    .friend {
                        .image {
                            flex-basis: 40%;
                        }
                        .friend-req-info {
                            flex-basis: 60%;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 350px) {
    .main-area {
        .container {
            .wrapper {
                .main-chat {
                    .chat-here {
                        .chat-header {
                            .chat-name {
                                span {
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .friends {
        .container {
            .wrapper {
                .main-friends {
                    .friend {
                        flex-direction: column;
                    }
                }
            }
        }
    }
}
