.profile {
    animation: fadeUp 0.5s ease forwards;
    .container {
        .wrapper {
            color: $text-primary;
            display: flex;
            flex-direction: column;

            padding: 8rem 1rem 3rem 1rem;
            h1 {
                font-size: 35px;
            }
            .main-profile {
                padding: 1rem;
                border-radius: 5px;
                box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                border: 1px solid rgb(218, 218, 218);
                display: flex;
                flex-direction: row;
                gap: 10px;
                .image {
                    flex-basis: 40%;
                    img {
                        display: block;
                        max-width: 100%;
                        object-fit: cover;
                        border-radius: 5px;
                    }
                }
                .info {
                    flex-basis: 60%;
                    .info-main {
                        word-break: break-all;
                        .infox {
                            font-size: 35px;
                        }
                        form {
                            input {
                                width: 100%;
                                height: 30px;
                            }
                            button {
                                margin-top: 0.3rem;
                                border: 1px solid black;
                                color: white;

                                padding: 0.4rem;
                                min-width: 200px;
                                border-radius: 5px;
                            }
                        }
                    }
                }
            }
        }
    }
}

.friends {
    animation: fadeUp 0.5s ease forwards;
    .container {
        .wrapper {
            padding: 1rem 1rem 3rem 1rem;
            h1 {
                user-select: none;
                font-size: 35px;
            }
            .main-friends {
                padding: 10px;
                box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                border: 1px solid rgb(218, 218, 218);
                display: grid;
                grid-template-columns: 1fr 1fr;
                gap: 10px;
                max-height: 300px;
                overflow-y: auto;
                border-radius: 5px;
                .friend {
                    animation: fadeUp 0.5s ease forwards;
                    display: flex;
                    flex-direction: row;
                    gap: 10px;
                    padding: 0.4rem;
                    background-color: white;
                    box-shadow: 0px 6px 13px 0px rgba(209, 209, 209, 1);
                    border: 1px solid rgb(218, 218, 218);
                    width: 100%;
                    .image {
                        flex-basis: 20%;
                        img {
                            display: block;
                            max-width: 100%;
                            object-fit: cover;
                            border-radius: 5px;
                            aspect-ratio: 1/1;
                        }
                    }
                    .friend-req-info {
                        flex-basis: 80%;
                        word-break: break-all;
                    }
                    .accept-btn {
                        button {
                            width: 100%;
                            height: 100%;
                            background-color: #27ae60;
                            color: white;
                            border: 0px solid black;
                            min-width: 40px;
                            border-top-right-radius: 5px;
                            border-bottom-right-radius: 5px;
                            transition: 0.3s background-color ease;
                            i {
                                font-size: 15px;
                                transition: 0.3s font-size ease;
                            }
                            &:hover {
                                background-color: #1f8b4c;
                            }
                            &:hover i {
                                font-size: 25px;
                            }
                        }
                    }
                }
            }
        }
    }
}
